import React, { useContext } from 'react'
import { darkmode } from '../context/darkmode'

const Header = ({theme,setTheme}) => {
  let Theme = useContext(darkmode);
  return ( 
    <div className='sticky top-0 font-bold  bg-white flex justify-between items-center py-1 md:py-3'>
      <div className= 'flex items-center h-fit'>
        <img className='w-10 md:w-20 h-auto md:rounded-' src={require('./weather.jpg')}/>
        <h1 className='md:text-xl text-base font-sans'>SkyBlueWeather</h1>
      </div>
      <div className='mr-3'>
        <span onClick={()=>setTheme(!theme)} className={Theme === false ? 'px-3 py-2 text-sm hover:bg-blue-300 rounded-lg transition duration-300 cursor-pointer select-none hover:text-white mr-2':
                                            'px-3 py-2 text-sm hover:bg-gray-400 rounded-lg transition duration-300 cursor-pointer select-none hover:text-white mr-2'}>Dark/Light</span>
      </div>
    </div>
    
  )
}

export default Header