import React, { useContext } from 'react'
import { darkmode } from '../context/darkmode'
import Loading from './Loading'
import './style.css'

const Cards = ({city}) => {
  let Theme = useContext(darkmode)

  if(JSON.stringify(city) !=='{}'){
    return (
      <div className={Theme === false ? 'mt-10 flex justify-between ml-14 card-ctn' :
                                        'mt-10 flex justify-between ml-14 text-slate-50 card-ctn'}>
        <div className='w-fit card-1'>
        <div className='text-4xl ml-6 font-bold'>{city.name},</div>
       <div className='text-3xl ml-8 mt-8 font-bold '>{city.sys.country}</div>
     <div className={Theme === false ? 'flex ml-6 items-center mt-20 px-8 py-2 w-fit h-fit bg-white shadow-md rounded-lg':
                                        'flex ml-6 items-center mt-20 px-8 py-2 w-fit h-fit bg-zinc-700 shadow-md rounded-lg'}>
            <div className='text-3xl font-bold ml-2'>{Math.ceil(city.main.temp)}C</div>
        </div>
        </div>
        <div className={Theme === false? 'w-fit mt-6 mr-20 text-center font-medium h-fit px-40 rounded-md py-4 bg-blue-100 card-2':
                                            'w-fit mt-6 mr-20 text-center font-medium h-fit px-40 rounded-md py-4 bg-gray-700 card-2'}>
          <div>Feels like{city.main.feels_like}C, {city.weather[0].description}. {city.weather[0].main}</div>
          <div className='mt-12'> {city.wind.speed}m/s NE</div>
          <div className='mt-12'>Humidity: {city.main.humidity}%</div>
          <div className='mt-12'>Visibility: {city.visibility / 1000}km</div>
          <div className='mt-12'>{city.main.pressure}hPa</div>
        </div>
      </div>
    )   }else{
        return(
          <Loading/>
        )
          
 }
}

export default Cards