const Err_card = ({City}) => {

  return (
    <div className='w-fit h-fit px-16 py-8 bg-white mx-auto mt-10 rounded-lg'>
      <img className='w-2/5 mx-auto pb-10 h-auto ' src={require(`./err.jpg`)} />
      <div className='text-center font-semibold'>Oops! We can't find the city you searched ,</div>
      <div className='text-center text-lg font-semibold mt-6'>" {City} "</div>
    </div>
  )
}

export default Err_card