import { click } from '@testing-library/user-event/dist/click';
import React, { useContext, useState } from 'react'
import { darkmode } from '../context/darkmode';
import Err_card from './Err_card';
import  './style.css'
const Sarch = ({city,fetchingWeather,error}) => {
  let [City,setCity] = useState('');
  let Theme = useContext(darkmode)

let clickiing =() => {
  fetchingWeather(City)
  console.log(City);
}

let changing = (event) => {
  setCity(event.target.value);
}

 if(error === false) {
  return (
    <div className='w-2/4 mt-6 mx-auto srh'>
      <input value={City} onChange={(event)=>changing(event)} className='bg-gray-50 pl-2 rounded-lg w-3/4 py-2' placeholder='Enter city name to search'/>
      <button onClick={clickiing} className='rounded-lg active:bg-gray-200 active:border-solid select-none cursor-pointer transition duration-300 w-fit h-fit ml-6 px-3 py-2 bg-white hover:bg-zinc-100 text-black font-bold text-base'>Search</button>
    </div>
  )
 }else {
  return (
  <div>
      <div className='w-2/4 mt-6 mx-auto'>
      <input value={City} onChange={(event)=>changing(event)} className='bg-gray-50 pl-2 rounded-lg w-3/4 py-2' placeholder='Enter city name to search'/>
      <button onClick={clickiing} className='rounded-lg active:bg-gray-200 active:border-solid select-none cursor-pointer transition duration-300 w-fit h-fit ml-6 px-3 py-2 bg-white hover:bg-zinc-100 text-black font-bold text-base'>Search</button>
    </div>
    <Err_card City={City}/>
  </div>
  )
 }
}

export default Sarch