import { useContext, useEffect, useState } from "react";
import { apikey, source } from "./api/api";
import Cards from "./components/Cards";
import Err_card from "./components/Err_card";
import Header from "./components/Header";
import Loading from "./components/Loading";
import Sarch from "./components/Sarch";
import { darkmode } from "./context/darkmode";


function App() {
let [city,setCity] = useState({});
let [error,setError] = useState(false);
let [theme,setTheme] = useState(false)

let fetchingWeather =async (dyncity) => {
  try {
    setError(false)
  let resource = await source.get(`/weather?q=${dyncity}&appid=${apikey}&units=metric`)
  setCity(resource.data)
  console.log(resource.data.weather[0].icon , resource.data)

  } catch (err) {
    setError(true)
  }  
}

useEffect(()=>{
  fetchingWeather('Yangon');
},[])

if(error === false) {
  return (
    <>
    <darkmode.Provider value={theme}>
    <div className={theme === false ? 'bg-blue-50 w-full h-[120vh] sm:w-full sm:h-full':
                                        'bg-slate-600 w-full h-[120vh] sm:w-full sm:h-full'}>
      <Header theme={theme} setTheme={setTheme}/>
      <Sarch city={city} fetchingWeather={fetchingWeather} error={error}/>
      <Cards city={city}/>
    </div>
    </darkmode.Provider>
    </>
      );
} else{
  console.log(error , 'is error');
  return(
    <>
  <darkmode.Provider>
  <div className={theme === false ? 'bg-blue-50 w-full h-full sm:w-full sm:h-full':
                                      'bg-slate-600 w-full h-full sm:w-full sm:h-[120vh]'}>
      <Header theme={theme} setTheme={setTheme}/>
      <Sarch city={city} fetchingWeather={fetchingWeather} error={error}/>
    </div>
  </darkmode.Provider>
    </>  )
}
}

export default App;
